body {
	background: #000000;
	margin: 0;
}

.playground {
	display: flex;
	background: #000000;
	transition: filter 0.5s cubic-bezier(0.19, 1, 0.22, 1);
	transition: -webkit-filter 0.5s cubic-bezier(0.19, 1, 0.22, 1);

	canvas {
		&[data-cursor="default"] { cursor: default; }
		&[data-cursor="crosshair"] { cursor: crosshair; }
		&[data-cursor="pointer"] { cursor: pointer; }
		&[data-cursor="move"] { cursor: move; }
		&[data-cursor="zoom-in"] { cursor: zoom-in; cursor: -webkit-zoom-in; cursor: -moz-zoom-in; }
		&[data-cursor="zoom-out"] { cursor: zoom-out; cursor: -webkit-zoom-out; cursor: -moz-zoom-out; }
		&[data-cursor="grab"] { cursor: grab; cursor: -webkit-grab; cursor: -moz-grab; }
		&[data-cursor="grabbing"] { cursor: grabbing; cursor: -webkit-grabbing; cursor: -moz-grabbing; }
		&[data-cursor="not-allowed"] { cursor: not-allowed; }
	}

	&.defocus {
		filter: blur(8px);
		-webkit-filter: blur(8px);
	}
}



